<template>
  <div class="moviedetail">
     
    <div class="mdetailbg" :style="{'background-image': 'linear-gradient(to bottom, rgba(245,245,245, 0), rgba(245,245,245, .3), rgba(245,245,245, .94)40%),url(' + movieDetailData.images.large + ')'}"></div>

    <!-- 头部icon -->
    <nav>
      <van-nav-bar left-text="返回" title="电影详情" left-arrow :border="false" :fixed="true" @click-left="back">
        <i class="share" slot="right" ></i>
      </van-nav-bar>
    </nav>

    <!-- 主要部分 -->
    <div class="mdetailbox">
      <!-- 播放器 -->
      <div class="player">
        <van-icon class="play" name="play-circle" size="42" data-play="0" @click="showPlay"/>
        <video controls id="video" class="video" v-if="isView">
          <source :src="movieDetailData.trailer_urls[0]" type="video/mp4" :class="{active: isView}">
          <!-- <source src="http://vt1.doubanio.com/201912191850/9074885ad934bb7d4e1eddeb6756f6db/view/movie/M/302560730.mp4" type="video/mp4"> -->
          <!-- 不支持 -->
        </video>
      </div>

      <!-- 影片基本信息 -->
      <div class="mdetail_mainInfo clearfix">
          <div class="mdinfo_img fl">
             <img class="autoimg" :src="movieDetailData.images.small" alt="">
          </div>
          <div class="mdinfo_msg fl">
            <div class="mi-title one-txt">{{movieDetailData.title}}</div>
            <div class="mi_grade">{{movieDetailData.rating.average}} 分</div>
            <div>时长：{{movieDetailData.durations[0]}}</div>
            <div class="type_language">
              <span>{{movieDetailData.genresInfo}}</span>
              <span>{{movieDetailData.languages[0]}}</span>
            </div>
            <div class="mi-info">
              <div>
                <i class="love"></i>
                <span>{{movieDetailData.comments}}</span>
              </div>
              <div>
                <i class="collect"  @click="likeMovie(movieDetailData)" :class="{active: likeActive}"></i>
                <span>{{movieDetailData.collects}}</span>
              </div>
            </div>
          </div>
      </div>

      <!-- 简介等其他信息 -->
      <div class="mdetail_introduction">
        <van-tabs  color="#FF429B">
          <van-tab title="简介">
            <!-- 剧情简介 -->
            <div class="synopsis">
                <div class="movie-detail-b">
                  <div class="mb-title">简介</div>
                  <p class="m-text" :style="{display}">{{movieDetailData.summary}}</p>
                </div>
                <div class="arrow" @click="changeDisplay">
                  <van-icon name="arrow-down" />
                </div>
            </div>

            <!-- 演员列表 -->
            <div class="actor">
                <div class="mb-title">演员</div>
                <div class="actorbox">
                  <div class="actorlist clearfix" :style="{width: movieDetailData.casts.length * movieDetailData.w +'px'}">
                    <div ref="actoritem" class="actoritem" v-for="(item, index) in movieDetailData.casts" :key="index">
                        <div class="actorimg">
                           <img class="autoimg" :src="item.avatars.small" alt="">
                        </div>
                        <div class="name one-txt">{{item.name}}</div>
                    </div>
                  </div>
                </div>
            </div>
          </van-tab>

          <van-tab title="剧照">
            <!-- 剧照 -->
            <div class="photo clearfix">
              <div class="photoimg fl" v-for="(p,i) in movieDetailData.photos" :key="i">
                <img class="autoimg" :src="p.image" alt="">
              </div>

              <!-- <div class="photoimg fl">
                <img class="autoimg" src="https://img9.doubanio.com/view/photo/l/public/p511131245.jpg" alt="">
              </div> -->
            </div>
          </van-tab>

          <van-tab title="影评">
            <!-- 热门评论 -->
            <div class="comments">
              <div class="commentslist">
                <div class="commentitem" v-for="(item,index) in movieDetailData.popular_reviews" :key="index">
                  <div class="user clearfix">
                    <div class="userimg fl" >
                      <img class="autoimg" :src="item.author.avatar" alt="">
                    </div>
                    <div class="username fl">{{item.author.name}}</div>
                  </div>
                  <div class="txt two-txt">{{item.summary}}</div>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>

      </div>

      <!-- 购票 -->
      <div class="footer clearfix">
        <div class="icon fl">
          <div class="fl">
            <i class="like"></i>
            <span>想看</span>
          </div>
          <div class="fl">
            <i class="liked"></i>
            <span>看过</span>
          </div>
        </div>
        <div class="buybtn fr" @click="buyTickets(movieDetailData.id)">
          <!-- color="linear-gradient(to right, #FF429B, #FF3472)" -->
          <van-button round type="info" color="linear-gradient(to right, #FF429B, #FF3472)" >购票</van-button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
    import { Dialog } from 'vant';
    import {createNamespacedHelpers} from 'vuex'
    const {mapState, mapMutations} = createNamespacedHelpers('movieDetailModule')
    
    export default {
        name:'movieDetail',

        data(){
          return{
            isView:false,
            active:0
          }
        },

        computed: {
          ...mapState(['movieDetailData',"display","likeActive"])
        },

        methods: {

          ...mapMutations(['changeDisplay','resetState']),

          // 返回上一步
          back() {
            this.$router.go(-1);
          },

          likeMovie(item){
            // console.log('item ==> ', item);

            //获取用户登录状态
            let userStatus = localStorage.getItem('user');
            // console.log('userStatus',userStatus);

            // 判断是否是登录状态
            if (userStatus){
              userStatus = JSON.parse(userStatus);

              // 没有登录
              if (!userStatus.isLogin) {

                Dialog.confirm({
                    title: '您还没有登录不能收藏该电影哦！',
                    message:'确定将自动跳转到登录页面'
                    }).then(() => {
                      return this.$router.push({name: 'login'});

                }).catch(() => {
                  // on cancel
                });

              }

              // 已经登录
              //获取用户收藏的电影
              let likeMovieData = localStorage.getItem('likeMovieData');
              likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : {};

              //记录当前用户收藏的电影
              let curLikedMovieData = likeMovieData[userStatus.username]; 

              if (!curLikedMovieData) {
                likeMovieData[userStatus.username] = [];
              }

            //根据电影的id判断是否已收藏该电影
            for (let i = 0; i < likeMovieData[userStatus.username].length; i++) {
              if (likeMovieData[userStatus.username][i].id == item.id) {
                //提示用户
                this.$toast('已经收藏过这个电影了');
                return;
              }
            }

              this.$store.commit('movieDetailModule/changelikeActive', true);

              //将电影推进likeMovieData[userStatus.username] 从头推进
              likeMovieData[userStatus.username].unshift(item);
              // console.log('likeMovieData ==> ', likeMovieData);

              localStorage.setItem('likeMovieData', JSON.stringify(likeMovieData));
              this.$toast.success('收藏成功');
              
            }else{
              // console.log('您还没有登录');
            Dialog.confirm({
                title: '您还没有登录不能收藏该电影哦！',
                message:'确定将自动跳转到登录页面'
                }).then(() => {
                  return this.$router.push({name: 'login'});

              }).catch(() => {
                // on cancel
              });
            }
            this.$toast.clear();
          },

          // 购票
          buyTickets(id){

            //获取用户登录状态
            let userStatus = localStorage.getItem('user');

            // 判断是否是登录状态
            if (userStatus){
              userStatus = JSON.parse(userStatus);

              // 没有登录
              if (!userStatus.isLogin) {
                Dialog.confirm({
                    title: '您还没有登录不能购买该电影哦！',
                    message:'确定将自动跳转到登录页面'
                    }).then(() => {
                      return this.$router.push({name: 'login'});
                }).catch(() => {
                  // on cancel
                });
              }

              // 已经登录
              this.$router.push({name:'buyTickets',query:{id}});
              
            }else{

              Dialog.confirm({
                  title: '您还没有登录不能购买该电影哦！',
                  message:'确定将自动跳转到登录页面'
                  }).then(() => {
                    return this.$router.push({name: 'login'});

                }).catch(() => {
                  // on cancel
                });
              }
              this.$toast.clear();  
          },

          showPlay(){
            this.isView = !this.isView;

              // let video = document.getElementById('video');
              // console.log('video=>',video);
              // var playStatus = this.dataset.play;
              // if (playStatus == 0) {
              //     //播放视频
              //     video.play();
              //     this.dataset.play = 1;
              //     this.textContent = '暂停';
              //   } else {
              //     //暂停视频
              //     video.pause();
              //     this.dataset.play = 0;
              //     this.textContent = '播放';
              // }
            }
        },   

        created() {
          //开启加载提示
          this.$toast.loading({
            duration: 0,
            message: '加载中...'
          });

          // 重置数据
          this.resetState();
          
          // 用于样式书写即将上映魔镜奇缘3 id ：30482829
          let id = this.$route.params.id; //截取路由参数
          // console.log('id ==> ', id);
          this.axios({
            method: 'GET',
            url: 'https://douban.uieee.com/v2/movie/subject/' + id
          }).then(MDetailresult => {
            //  console.log('MDetailresult ==> ', MDetailresult);

            //将电影类型合并
            MDetailresult.data.genresInfo =MDetailresult.data.genres.join(' / ');
            // console.log( MDetailresult.data.genresInfo);
            //千位以k显示，万位w显示，不足1000，直接显示
            let comments = MDetailresult.data.comments_count;
            MDetailresult.data.comments = comments >= 10000 ? (comments / 10000).toFixed(1) 
            + 'w' : comments >= 1000 ? (comments / 1000).toFixed(1) + 'k' : comments;
            let collects = MDetailresult.data.collect_count;
            MDetailresult.data.collects = collects >= 10000 ? (collects / 10000).toFixed(1) 
            + 'w' : collects >= 1000 ? (collects / 1000).toFixed(1) + 'k' : collects;
            //演员item的宽度
            MDetailresult.data.w = 0;
            
            // console.log('MDetailresult ==> ', MDetailresult);
            // 提交数据过去
            this.$store
            .dispatch("movieDetailModule/getMDetailData",MDetailresult.data)
            .then(() =>{
              let actorItemW= this.$refs.actoritem[0].clientWidth;
              // console.log('actorItemW ==> ', actorItemW);
              this.$store.commit('movieDetailModule/changeW', actorItemW);

              //根据用户登录状态，判断该电影是否被该用户收藏
              let userStatus = localStorage.getItem('user');
              let isActive = userStatus && JSON.parse(userStatus).isLogin;

              this.$toast.clear();

              //用户登录
              if (isActive) {
                 let username = JSON.parse(userStatus).username;

                  //所有用户收藏的电影
                  let likeMovieData = localStorage.getItem('likeMovieData');

                  likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : {};

                  //获取当前用户收藏的电影
                  let curLikeMovieData = likeMovieData[username];

                  curLikeMovieData = curLikeMovieData ? curLikeMovieData : []

                  for (let i = 0; i < curLikeMovieData.length; i++) {
                    if (this.movieDetailData.id == curLikeMovieData[i].id) {
                      this.$store.commit('movieDetailModule/changelikeActive', true);
                      return;
                    }
                  }
                  this.$store.commit('movieDetailModule/changelikeActive', false);

              }else{
                this.$store.commit('movieDetailModule/changelikeActive', false);

              }

              this.$toast.clear();
            })
          }).catch(err => {
            // console.log('err ==> ', err);
            this.$toast.clear();
          })
        }
    }
</script>

<style lang="less" scoped>
  .moviedetail{

    .mdetailbg{
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      z-index: -1;
      filter: blur(2px);
      opacity: .9;
    }
    nav{
        width: 100%;
        position: fixed;
        top: 0;
        height: 50px;
        background-color: #697A9C;
        /deep/.van-nav-bar{
          background-color: transparent;
        }
        .van-nav-bar__text{
          color: #fff;
          font-size: 16px;
        }
        .van-nav-bar__title {
            color:#fff;
            font-size: 18px;
        }
        .van-icon-arrow-left:before{
            color: #fff;
            font-size: 20px;
        }
        .share{
          display: block;
          width: 36px;
          height: 36px;
          background: url("../assets/images/share.png") no-repeat center center;
          background-size:36px 36px;
        }
    }
    
    .mdetailbox{
      position:fixed;
      top:50px;
      bottom:50px;
      right:0;
      left:0;
      overflow-y:auto;
      .player{
        height:100px;
        position:relative;
        .play{
          width: 45px;
          height:45px;
          color: #fff;
          position:absolute;
          top:0;
          bottom:0;
          left:0;
          right:0;
          margin:auto;
        }
        video{
          position: fixed;
          top:20%;
          width:100%;
          z-index: 100;
        }
      }
      
      .mdetail_mainInfo{
        padding:20px 10px;
        margin-top: 10px;
        box-shadow: 2px 3px 4px 2px #ccc;
        border-radius: 4px;
        .mdinfo_img{
          width: 100px;
          >img{
            margin-top: -40px;
            overflow: hidden;
            border-radius: 5px;
          }
        }

        .mdinfo_msg{
          margin-top:-8px;
          width: calc(~"100% - 115px");
          margin-left: 15px;
          position: relative;
          >div{
            line-height: 26px;
          }
          .mi-title{
            // width: calc(~"100% - 120px");
            font-size: 18px;
            color: #000;
            font-weight: 600;
          }
          .type_language{
            span{
              margin-right: 20px;
            }
          }
          .mi-info{
            font-size: 12px;
            color: #888;
            height: 16px;
            margin-top:4px;
            >div{
              padding:2px 4px;
              float: left;
              margin-right: 10px;
              line-height: 16px;
              border: 1px solid #aaa;
              .love{
                display:block;
                float:left;
                width:20px;
                height:20px;
                background:url("../assets/images/like_love.png") no-repeat center center;
                background-size:18px 18px;

                &.active{
                  background:url("../assets/images/like_love_active.png") no-repeat center center;
                  background-size:18px 18px;
                }
              }
              .collect{
                display:block;
                float:left;
                width:20px;
                height:20px;
                background:url("../assets/images/collect_star.png") no-repeat center center;
                background-size:20px 20px;

                &.active{
                  background:url("../assets/images/collect_star_active.png") no-repeat center center;
                  background-size:20px 20px;
                }
              }
              span{
                padding:4px;
              }
            }
          }
          .mi_grade{
            font-size: 24px;
            font-weight: 700;
            color: rgb(255, 1, 141);
            position: absolute;
            right: 0px;
            top: 30px;
          }
        }
      }

      .mdetail_introduction{
        margin-top: 14px;
        padding: 10px;
        background-color: #fff;
        /deep/.van-tabs__nav {
         background: #f5f5ff;
        }
        /deep/.van-tab--active {
          color: #FF3472;
          font-size: 20px;
          font-weight: bold
        }

        .mb-title{
          margin: 8px 0 4px;
          font-size: 18px;
          color: #000;
          font-weight: 600;
          padding-bottom: 6px;
          border-bottom: 1px solid #697A9C;
        }
      }

      .synopsis{
          padding: 10px 0;
          position: relative;
          .m-text{
            padding: 0 2px;
            font-size: 14px;
            color: #333;
            line-height: 24px;
            text-indent: 20px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
          }
          .arrow{
            position: absolute;
            width: 20px;
            height: 20px;
            left: 0;
            right: 0;
            bottom:0;
            margin: 0 auto;
            font-size: 16px;
            text-align: center;
            line-height: 20px;
            color: rgb(255, 1, 141);
            
          }
      }

      .actor{
          margin-top: 20px;
          position: relative;
          .actorbox{
            overflow-x: auto;
            overflow-y: hidden;
            .actorlist{
              width: 112px;
              .actoritem{
                float: left;
                width: 100px;
                padding: 6px;
                .actorimg{
                  // border-radius: 50%;
                  overflow: hidden;
                }
                .name{
                  margin: 10px 0;
                  text-align: center;
                  font-size: 12px;
                  color: #222;
                }
              }
            }
          }
      }

      // 瀑布流布局
      .photo{
        margin: 10px 0;
        column-count: 3;
        column-gap: 10px;
        .photoimg{
          margin-bottom: 6px;
        }
      }
      
      .comments{
          margin-top: 20px;
          .commentslist{
            .commentitem{
              margin: 8px 0;
              background-color: #fff;
              opacity: .5px;
              padding: 6px;
              border-bottom: 1px solid #697A9C;
              border-right: 1px solid #697A9C;
              .user{
                .userimg{
                  width: 40px;
                  height: 40px;
                  border-radius: 50%;
                  >img{
                    border-radius: 50%;
                  }
                }
                .username{
                  font-size: 18px;
                  color: #000;
                  margin-left: 20px;
                  margin-top: 14px;
                }
              }
              .txt{
                margin-top: 8px;
                color: #222;
                font-size: 14px;
                line-height: 20px;
                text-indent: 20px;
              }
            }
          }
      }

      // !!!!!!
    }

    .footer{
      width: 100%;
      height: 48px;
      position: fixed;
      bottom: 0;
      background-color:#fff;
      .buybtn{
        padding: 0 10px;
        /deep/.van-button{
          width:200px;
          font-size: 18px;
        }
      }

      .icon{
        padding: 0 10px;
        div{
          margin-right: 30px;
          font-size: 14px;
          text-align: center;
          color: #333;
        }
        .like{
          display: block;
          width: 30px;
          height: 30px;
          background:url("../assets/images/like_love.png") no-repeat center center;
          background-size: 28px 28px;
        }
        .liked{
          display: block;
          width: 30px;
          height: 30px;
          background:url("../assets/images/collect_star.png") no-repeat center center;
          background-size: 30px 30px;
        }
      }
    }
  }
</style>